import { Component, OnInit } from '@angular/core';
import { CakesService } from './cakes.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'Rate My Cakes';
  cakes = [];
  newCake: any;
  display = false;
  newRating: any={};
  selectedCake: any;
  avg: any;

  constructor(private cakesService: CakesService) {}

  ngOnInit(){
  	this.getCakesFromService();
  	this.newCake = { baker: "", image: ""};
  }

  getCakesFromService(): void{
  	let observable = this.cakesService.getCakes();
  	observable.subscribe(<Object>(data)=>{
  		this.cakes = data;
  		console.log("Here is the info for", this.cakes);
  	})
  }
  onSubmit(){
  	let observable = this.cakesService.addCake(this.newCake);
  	observable.subscribe(data=>{
  		this.newCake = { baker: "", image: ""};
  		this.getCakesFromService();
  	})
  }
  info(idx){
  	this.selectedCake = this.cakes[idx];
  	var sum = 0;
  	for(var i = 0; i < this.selectedCake.ratings.length; i++){
  		sum += this.selectedCake.ratings[i].stars;
  	}
  	this.avg = sum/this.selectedCake.ratings.length;
  }
  rateSubmit(ratingform, cakeId){
  	console.log(cakeId);
  	console.log(ratingform.value)
  	let observable = this.cakesService.addRating(ratingform.value, cakeId);
  	observable.subscribe((data: any)=>{
  		console.log("COMMENT CREATED");
  		this.cakes = this.cakes.map(cake=>{
  			return cake._id === data._id ? data: cake
  		})
  	})
  	this.newRating = { stars: "", comment: ""};
  }
}
